import React from 'react';
import Project from './Project';
import {Row} from 'react-bootstrap'
const Projects = () => {
	return (
		<div id="projandintern">
			<h3>Projects and Internships</h3>
            <Row className="justify-content-center">
                <Project
                    title="Resume Builder"
                    duration="July 2020"
                    teamSize={2}
                    description="A simple, clean, easy to use Resume bulider for your next tech job"
                    technologies={["JavaScript", "React.js"]}
                />
                <Project
                    title="Piano Chords player"
                    duration="July 2020"
                    teamSize={"Individual"}
                    description="This is a fun app that can be used to generater chords for songs if you're not a music expert"
                    technologies={["JavaScript", "React.js"]}
                />
                <Project
                    title="Timetable Generator Using Genetic Algorithms"
                    duration="Feb 2020 – July 2020"
                    teamSize={2}
                    description=" This Timetable generator is a full-fleshed Web-App powered by the Genetic Algorithm, can be used to schedule classes for the whole university. This timetable generator is not only limited to Galgotias University, and can be easily configured for other schools and colleges"
                    technologies={["Python", "JavaScript", "React.js", "GraphQL"]}
                />
                <Project
                    title="Nikhilsati.github.io/me/"
                    duration="May 2020"
                    teamSize={"Individual"}
                    description="The same Website you are viewing right now. Made with ❤ and React"
                    technologies={[ 'React' ]}
                />
                <Project
                    title="Mess Management System"
                    duration="Feb 2020 – April 2020"
                    teamSize={2}
                    description=" This Mess Management System allows the student to Rate Food, Give Feedback, Choose and View menu, and allows admin the view the feedback and help them generate better a Timetable. "
                    technologies={[ 'JavaScript', 'React', 'Firebase' ]}
                />
            </Row>
			
		</div>
	);
};

export default Projects;
