import React from 'react';
import { Image, Row, Col } from 'react-bootstrap';
const Refrence = ({name, email, position, image, description}) => {
	return (
		<Row className="refrence justify-content-center align-items-center">
			<Col xs={5} md={4} lg={3}>
				<Image src={image} roundedCircle className="image" />
			</Col>
			<Col xs={7} md={6} lg={5} className="description">
				<h4>{name}</h4>
				<p>
					<span>{position} | </span>
					<span>
						<i>{email}</i>
					</span>
				</p>
				<p>
					"{description}"
				</p>
			</Col>
		</Row>
	);
};

export default Refrence;
