import React, { useState } from 'react'
import { Link } from 'react-scroll'

const Menu = () => {
    const [open, setOpen] = useState(false)
    const colors = ['bg-red', 'bg-green', 'bg-yellow', 'bg-blue', 'bg-purple']
    const closeMenu = () => setOpen(false)
    return (
        <div style={{position: 'fixed', top: '10px', zIndex: 99}}>
            <Link href="/#" id="menu"><img alt="menu"  src={require('../../assets/menu-icon.svg')} onClick = {() => setOpen(!open)}/></Link>
            <div className={`menu ${open?'menu-open':''}`}>
                <Link to="about-me" smooth={true} onClick={closeMenu} offset={-100} className={colors[0]}>About</Link>
                <Link to="skills" smooth={true} onClick={closeMenu}  className={colors[1]}>Skills</Link>
                <Link to="projandintern" smooth={true} onClick={closeMenu}  className={colors[2]}>Projects</Link>
                <Link to="education" smooth={true} onClick={closeMenu}  className={colors[3]}>Education</Link>
                <Link to="refrences " smooth={true} onClick={closeMenu}  className={colors[4]}>Refrences</Link>
            </div>
        </div>
    )
}

export default Menu
