import React from 'react';
import './Refrences.css';
import Refrence from './Refrence';
import { Carousel } from 'react-bootstrap';
const Refrences = () => {
	return (
		<div id="refrences">
			<h3>Refrences</h3>
			<Carousel indicators={false} nextIcon={<span><img class="controls" src={require('../../assets/next.png')}/></span>} prevIcon={<span><img class="controls" src={require('../../assets/prev.png')}/></span>}>
				<Carousel.Item>
					<Refrence
						name="Kunal Pant"
						email="kunalpant95@gmail.com"
						position="Software Developer"
						image={require('../../assets/kunal.jfif')}
						description="I rarely come across genius minds who stand out like Nikhil. I am fortunate to have worked with him. He is a technologist with unbelievable problem-solving skills who is not only interested in the project development but also in his teammates' professional growth."
					/>
				</Carousel.Item>
				
			</Carousel>
		</div>
	);
};

export default Refrences;
