import React from 'react'

const SkillLevel = (props) => {
    return (
        <>
            <svg id="level" data-name="level" xmlns="http://www.w3.org/2000/svg" width="100%" height="10" viewBox="0 0 100% 5">
                <rect id="Rectangle_11" data-name="Rectangle 11" width="100%" height="100%" rx="5.6" fill="#dfdfdf"/>
                <rect id="Rectangle_7" data-name="Rectangle 7" width={`${props.level * 10}%`} height="100%" rx="5.6" className={`fill-${props.color}`}/>
            </svg>
        </>
    )
}

export default SkillLevel
