import React from 'react';
import { Row, Col } from 'react-bootstrap';
import './AboutMe.css';
const AboutMe = () => {
	return (
		<div id="about-me">
			<h3>About Me</h3>
			<Row className="justify-content-center align-items-center">
				<Col xs={6} sm={5} md={4} lg={3}>
					<img className="sitting-boy" alt="sitting boy" src={require('../../assets/illustration-1.svg')} />
				</Col>
				<Col xs={9} lg={6}>
					<p>
						Hi, I'm a Software Engineer. I love to work on new technologies, like React.js. In my free time,
						I enjoy solving my common life problem through programming. I love competitive programming,
						music, and writing. <br/>I'll be graduating next year and I'm currently open to full-time job
						opportunities.
					</p>
				</Col>
			</Row>
		</div>
	);
};

export default AboutMe;
