import React from 'react'
import {Col, Row} from 'react-bootstrap';

import Skill from './Skill'
const Skills = () => {
    const colors = ['red', 'green', 'yellow', 'blue', 'purple']
    return (
        <>
            <div id="skills">
                <h3>Skills</h3>
                <Row className="justify-content-center">
                    <Col xs={10} lg={6}>
                        <Skill 
                            name="JavaScript"
                            level={7}
                            description={"Intermediate"}
                            image={require('../../assets/JS.png')}
                            color={colors[0]}
                        />
                    </Col>
                    <Col xs={10} lg={6}>
                        <Skill 
                            name="C++"
                            level={5}
                            description={"Intermediate"}
                            image={require('../../assets/c++.svg')}
                            color={colors[1]}
                        />
                    </Col>
                </Row>
                <Row className="justify-content-center">
                    <Col xs={10} lg={6}>
                        <Skill 
                            name="Python"
                            level={6}
                            description={"Intermediate"}
                            image={require('../../assets/python.png')}
                            color={colors[2]}
                        />
                    </Col>
                    <Col xs={10} lg={6}>
                        <Skill 
                            name="Web Development"
                            level={8}
                            description={"Advance"}
                            image={require('../../assets/webDev.png')}
                            color={colors[3]}
                        />
                    </Col>
                </Row>    
            </div>
            <div id="toolsandtech">
            <h3>Tools and Technology</h3>
            <Row className="justify-content-center">
                <Col xs={10} lg={6}>
                    <Skill 
                        name="React.js"
                        level={7}
                        description={"Intermediate"}
                        image={require('../../assets/React.png')}
                        color={colors[4]}
                    />
                </Col>
                <Col xs={10} lg={6}>
                    <Skill 
                        name="React Native"
                        level={3}
                        description={"Beginner"}
                        image={require('../../assets/ReactNative.png')}
                        color={colors[2]}
                    />
                </Col>
            </Row>
            <Row className="justify-content-center">
                <Col xs={10} lg={6}>
                    <Skill 
                        name="AWS"
                        level={3}
                        description={"Beginner"}
                        image={require('../../assets/AWS.png')}
                        color={colors[1]}
                    />
                </Col>
                <Col xs={10} lg={6}>
                    <Skill 
                        name="GraphQL"
                        level={5}
                        description={"Intermediate"}
                        image={require('../../assets/GraphQL.png')}
                        color={colors[0]}
                    />
                </Col>
            </Row>
        </div>
        </>
    )
}

export default Skills
