import React from 'react';
import { Row, Col } from 'react-bootstrap';
import './Education.css';
const Education = () => {
	return (
		<div id="education">
			<h3>Education and Extra-curriculars</h3>
			<Row className="justify-content-center">
				<Col xs={12} lg={6} className="education-col">
					<img alt="grad icon" className="edu-icon" src={require('../../assets/graduation-icon.png')} />
					<div>
					<h4>Computer Science Engineering,</h4>
					<p>
						<strong>Galgotias University</strong>, Greater Noida<br />
						2017-2021, 8.77 CGPA
					</p>
					</div>
				</Col>
			</Row>
            <Row className="justify-content-center">
				<Col xs={12} lg={6} className="education-col">
					<img alt="grad icon" className="edu-icon" src={require('../../assets/certificate-icon.png')} />
					<div>
					<h4>Submitted book chapter,</h4>
					<p>
						Machine Learning Algorithms for Healthcare Data Analytics<br />
						2020, CRC Press
					</p>
					</div>
				</Col>
			</Row><Row className="justify-content-center">
				<Col xs={12} lg={6} className="education-col">
					<img alt="grad icon" className="edu-icon" src={require('../../assets/volunteer-icon.png')} />
					<div>
					<h4>Volunteer for Lens Foundation,</h4>
					<p>
						Developed an online blog website for providing information<br />
						about Learning Disabilities using WordPress.<br/>
						2020, Lens Foundation
					</p>
					</div>
				</Col>
			</Row>
		</div>
	);
};

export default Education;
