import React from 'react'
import './Introduction.css'
import Menu from './Menu'
import Type from '../typeIt/Type'
import { Link } from 'react-scroll'
import SocialBar from '../social/SocialBar';
const Introduction = () => {
    return (
        <div id="introduction">  
            <Menu />
            <div className="d-flex justify-content-center align-items-center flex-column intro-content">
                    <div className="typeit">
                        <Type 
                            strings={[
                            "(●'◡'●) ",
                            "Hey! It's <span class='fon'>Nikhil!</span>",
                            ]}
                        />
                    </div>
                <Link to="about-me" offset={-100} smooth={true}><button>Know More</button></Link>
                <SocialBar />
                
            </div>
        </div>
    )
}

export default Introduction
