import React from 'react'
import { Col } from 'react-bootstrap';
import Tag  from './Tag'
const Project = ({title, duration, teamSize, description, technologies}) => {
    const colors = ['red', 'green', 'yellow', 'blue', 'purple']
    return (
        <Col className="project" xs={12} lg={9}>
            <h5>{title}</h5>
            <p>{duration} | <strong>Team Size:</strong> {teamSize}</p>
            <p>{description}</p>
            <div className="d-flex flex-wrap">
                {
                    technologies.map( (item, index) => <Tag color={colors[index%5]}>{item}</Tag>)
                }
            </div>
        </Col>
    )
}

export default Project
